%btn{
    border-radius: 20px;
    font-family: "DM Sans";
    font-size: 8px;
    line-height: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 62.4px;
    height: 24px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0;
    @media screen and (min-width: 1200px){
        font-size: 14px;
        line-height: 18px;
        min-width: 102px;
        height: 40px;
    }
}

.hero-white-button{
    @extend %btn;
    background: $main-color;
    color: #D41443;

    &:hover, &:focus{
        background: #ecebeb;
        border: 1px solid transparent;
        outline-color: transparent;
    }
}

.hero-red-button{
    @extend %btn;
    margin-right: 8px;
    color: $main-color;
    background: #D41443;

/*     &:hover, &:focus{
        background: $main-color;
        color: #D41443;
        border: 1px solid transparent;
    } */
}

.hero {
    background-color: $main-bg;
    height: 502px;

    @media screen and (min-width: 768px) {
        height: 399px;
    }

    @media screen and (min-width: 1200px) {
        height: 665px;
    }
}


.hero-container{
    position: relative;
    height: 100%;
/*     width: 100%; */

    @media screen  and (max-width: 479px) {
        margin: 0 auto;
        background-size: 171px 410px, 346px 344px;
        background-repeat: no-repeat, no-repeat;
        background-position: top 92px left 117px, top 83px left 36px;
        overflow: hidden;
    }
    @media screen and (min-width: 480px) {
        margin: 0 auto;
        background-size: 171px 410px, 346px 344px;
        //background-image: url(.././img/slider-pic7-1.png), url(.././img/ellipse-3.png);
        background-repeat: no-repeat, no-repeat;
        background-position: top 92px right 70px, top 83px right -40px;
    }
    @media screen and (min-device-pixel-ratio: 2),
    
            screen and (-webkit-min-device-pixel-ratio: 2),
            screen and (-o-min-device-pixel-ratio: 2),
            screen and (min-resolution: 192dpi),
            screen and (min-resolution: 2dppx) {
              .hero-container{

              }   
            }
        @media screen and (min-width: 768px) {
        max-width: 768px;
        margin: 0 auto;
        background-size: 168px 133px, 143px, 324px 322px, 124px 126px;
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
        background-position: top 266px right 34px, top 63px left 378px, top 31px left 307px, top 245px left 34px;
        }

        @media screen and (min-width: 768px) {
        @media screen and (min-device-pixel-ratio: 2),
            screen and (-webkit-min-device-pixel-ratio: 2),
            screen and (-o-min-device-pixel-ratio: 2/1),
            screen and (min-resolution: 192dpi),
            screen and (min-resolution: 2dppx) {
              .hero-container{
              
              }
          } 
        }
        @media screen and (min-width: 1200px) {
        min-width: 1200px;
        margin: 0 auto;
        background-size: 293px 232px, 236px 557px, 538px 538px, 188px 191px;
        /* background-image: url(.././img/slider-pic4-1.png), url(.././img/slider-pic7-1.png),
            url(.././img/ellipse-3.png), url(.././img/slider-pic5-1.png); */
        background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
        background-position: bottom right 30px, bottom left 595px, top 52px left 476px, bottom 45px left 78px;
        }

        
        @media screen and (min-width: 1280px) {
            @media screen and (min-device-pixel-ratio: 2),
                screen and (-webkit-min-device-pixel-ratio: 2),
                screen and (-o-min-device-pixel-ratio: 2/1),
                screen and (min-resolution: 192dpi),
                screen and (min-resolution: 2dppx) {              .hero-container{
              
              }
            }
        }
}

.hero-title-container {
    display: block;
    min-height: 106px;
    padding-top: 193px;
    color: #ffffff;
    width: 218px;

    @media screen and (min-width:480px){
        margin-bottom: 160px;

    }
    @media screen and (min-width:768px){
         padding-top: 102px;
        margin-bottom: 173px; 
        width: 162px;
    }
    @media screen and (min-width:1200px){
        padding-top: 171px;
       width: 277px;
}
}

.hero-title-text {
  display: inline-block;
  font-family: "Titan One";
  font-style: 400;
  font-weight: normal;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-bottom: 19px;
  color: $main-color;
  @media screen and (min-width:480px){

      font-size: 22px;
      line-height: 1.1363;
  }
  @media screen and (min-width:768px){
      width: 150px;
      font-size: 22px;
      line-height: 1.1363;
  }
  @media screen and (min-width:1200px){
      width: 277px;
      font-size: 38px;
      line-height:1.1579;
  }

  span {
    font-size: 48px;
    line-height: 0.89;
    color: #d41443;
  }
}

.hero-btn-container{
    display: inline-flex;
}

.hero-text-description {
    display: none;

  @media screen and (min-width: 768px){
      position: absolute;
      bottom:59px;
      left:154px;
      display: inline-block;
      width: 114px;
      height: 48px;

      font-family: "DM Sans";
      font-size: 8px;
      line-height: 1.50;
      }
  @media screen and (min-width: 1200px){
      width: 200px;
      height: 78px;

      bottom:102px;
      left:295px;

      font-size: 14px;
      line-height: 1.40;
  }
}

.hero-arrow{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    margin-top: 8px;
    fill: $accent-color;
    border-radius: 50%;
    border: none;
    background-color: $main-color;
    
    @media screen and (min-width:1200px){
        margin-top: 20px;
    }

    &:hover, &:focus{
        background-color: #ecebeb;
        fill: $main-color;
        border: 1px solid $main-color;
    }
}

.hero-count{
    display: none;

    @media screen and (min-width:768px){
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        margin-top: 96px;
        margin-right: 44px;
    }
    @media screen and (min-width:1200px){
        margin-top: 160px;
        margin-right: 115px;
    }
}

.hero-count-text{
    display: none;
    @media screen and (min-width: 768px){
        position: relative;
        display: block;
        font-family: "Titan One";
        font-size: 28px;
        line-height: 1.1429;
        text-align: right;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: $accent-color;

        
    }
    @media screen and (min-width: 1200px){
        font-size: 46px;
        line-height: 1.1522;
    }
}

.hero-count-desc {
    display: none;
    @media screen and (min-width:768px){
    display: block;
    text-transform: lowercase;
    font-family: "DM Sans";
    font-size: 8px;
    line-height: 1.25;
    text-align: right;
    color: #ffffff;
    }
    @media screen and (min-width:1200px){
        font-size: 14px;
        line-height: 1.2857;
    }
}


.hero-circle-white{
    display: none;

  @media screen and (min-width:768px){

    position: absolute;
    top: 0;
    right: 0;
    transform: translate(25%, -25%);
    display: block;
    width: 16px;
    height: 16px;
    background-color: #ffffff;
    border: 0;
    border-radius: 50%;
    z-index: 0;
    }

  @media screen and (min-width:1200px){
    content:"";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(25%, -25%);
    display: block;
    width: 30px;
    height: 30px;
    background-color: #ffffff;
    border: 0;
    border-radius: 50%;  
  }

}